<template>
  <div class="search-options">
    <div class="opt-item expand">
      <label>关键字：</label>
      <el-input type="text" v-model="keywordValue" @input="onInput" />
    </div>
    <slot name="default"></slot>
    <el-button type="primary" @click="$emit('search')">搜索</el-button>
  </div>
</template>

<script>
export default {
  name: 'search-options',
  emits: ['search', 'update:keyword', 'keyword-change'],
  props: {
    keyword: {
      default: () => '',
      type: String,
    },
  },
  data() {
    return {
      keywordValue: this.keyword,
    };
  },
  methods: {
    onInput(val) {
      this.$emit('update:keyword', val);
      this.$emit('keyword-change', val);
    },
  },
};
</script>

